import React from "react";
import experiences from "../../experiences.js";


function ExperienceIcon(props) {
    return (
        <div className="col-sm-2 position-relative experience-card-left">
            <img className="experience-icon" src={props.icon} alt="Johns Hopkins University Logo"/>
        </div>
    )
}

function ExperienceDescription(props) {

    return (
        <ul id={"experience-description-" + props.id}>
            {props.description.map((lineItem, index) => {
                return <li key={index}>{lineItem}</li>
            })}
        </ul>
    );
}

function ExperienceContent(props) {

    function clickEvent(event) {
        console.log(event);
        console.log(event.target);
    }

    return (
        <div className="col-sm-10 experience-content">
            <h4>{props.position} <span className="left-align experience-date">{props.date}</span></h4>
            <h5>{props.company} <span className="left-align">{props.location}</span></h5>
            <ExperienceDescription description={props.description} id={props.id}/>
            {props.skills.map((skillItem, index) => {
                return <span key={index} className="badge rounded-pill bg-primary skill-token"> {skillItem} </span>
            })}
            <button id={"experience-button-" + props.id} onClick={clickEvent} className={"btn btn-outline-primary expand-button btn-sm d-none"}>Expand</button>
        </div>
    )
}

function ExperienceCard(props) {
    return (
        <div className="col-xl-6">
            <div className="row experience-card rounded">
                <ExperienceIcon 
                    icon={props.experience.icon}
                />
                <ExperienceContent 
                    position={props.experience.position} 
                    description={props.experience.description}
                    company={props.experience.company}
                    date={props.experience.dates}
                    skills={props.experience.skills}
                    location={props.experience.location}
                    id={props.id}
                />
            </div>
        </div>
    )
}


function HomeExperience() {

    return (
        <section id="home-experience">
            <div className="home-experience">
                <h2>Experience</h2>
                <div className="row">
                    {experiences.map((item)=>{
                        return <ExperienceCard key={item.id} id={item.id} experience={item} />
                    })}
                </div>
            </div>
        </section>
    )
}

export default HomeExperience;

