      "The Sudoku Solver is a Python GUI App that automatically reads the contents of a sudoku on the user screen and solves that sudoku using a mixture of both heuristic approaches and recursive bifurcations. To use, resize the GUI over an on-screen sudoku such that the sudoku fits within the window of the GUI. Then, press the 'Solve' button. The app will take a few seconds to read the sudoku board within its window, and once read, it will automatically compute the solution to the sudoku board, if it exists. The GUI can also automatically fill in the solution of the puzzle on screen, shown in the second gif."
